
*{
  -ms-touch-action: none;
      touch-action: none;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
  /* background-color: #f5f3f3; */
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
	text-decoration: none;
}
#app {
  color: #111;
}

.tabbar-wrapper {
  height: 3.3125rem;
}
.tabbar-wrapper .weui-tabbar {
  background: #fff;
}
.tabbar-wrapper .weui-tabbar .weui-tabbar__item .weui-tabbar__icon img {
  width: 80%;
  height: 80%;
}
.tabbar-wrapper .weui-tabbar .weui-tabbar__item .weui-tabbar__label {
  color: #111;
}
.tabbar-wrapper .weui-tabbar .weui-tabbar__item.weui-bar__item_on .weui-tabbar__label {
  color: #e62e46;
}
